<template>
    <!--<Collapse v-model="value1">-->
    <!--<Panel v-for="i in tables">-->
    <!--{{tables[i].name}}-->
    <!--<p slot="content">史蒂夫·乔布斯（Steve Jobs），1955年2月24日生于美国加利福尼亚州旧金山，美国发明家、企业家、美国苹果公司联合创办人。</p>-->
    <!--</Panel>-->
    <!--</Collapse>-->
    <div>
        <Collapse v-model="value1">
            <Panel v-for="i in tables">
                {{i.name}}
                <div slot="content">
                    <!--<Card :bordered="false" dis-hover>-->
                        <!--<p slot="title">添加菜单</p>-->
                        <!--<Input type="text" v-model="New_menu.name" placeholder="菜单项名称"/>-->
                        <!--<Input type="text" v-model="New_menu.url" placeholder="菜单项链接"/>-->
                        <!--<Button @click="New_menu_post">提交</Button>-->
                    <!--</Card>-->
                    <Button type="primary" style="margin-top:0;margin-bottom: 15px;">分类修改</Button>
                    <Button type="error" style="margin-top:0;margin-bottom: 15px;">删除分类</Button>
                    <Panel>
                        添加菜单
                        <div slot="content">
                            <Input type="text" v-model="New_menu.name" placeholder="菜单项名称"/>
                            <Input type="text" v-model="New_menu.url" placeholder="菜单项链接"/>
                            <Button @click="New_menu_post">提交</Button>
                        </div>
                    </Panel>
                    <Table :columns="i.columns" :data="i.data"></Table>
                    <Button type="success" long>提交该分类变更</Button>
                    <!--<div v-for="j in i.data">{{j.Name}}</div>-->
                </div>
            </Panel>
        </Collapse>

        <Modal v-model="Change_menu.status" title="Common Modal dialog box title" @on-ok="ChangeOk" @on-cancel="">
            <Input v-model="Change_menu.name" type="text"/>
            <Input v-model="Change_menu.url" type="text"/>
        </Modal>
    </div>
    <!--<p v-for="i in tables">32432443</p>-->
</template>

<script>
  export default {
    name: 'nav.vue',
    data: () => {
      return {
        value1: '1',
        tables: {},
        New_menu: {
          name: '',
          url: ''
        },
        Change_menu:{
          status:false,
          i:-1,
          j:-1,
          name: '',
          url: ''
        }
      }
    },
    methods: {
      change:function (i,j){
        console.log("分类"+i+"数字"+j+"改变")
        this.Change_menu.i=i;
        this.Change_menu.j=j;
        this.Change_menu.name=this.tables[i].data[j].Name;
        this.Change_menu.url=this.tables[i].data[j].Url;
        this.Change_menu.status=true;
      },
      ChangeOk:function (){
        this.$set(this.tables[this.Change_menu.i].data, this.Change_menu.j, {
          Name: this.Change_menu.name,
          Url: this.Change_menu.url
        })
        this.$Message.success('修改成功');
      },
      delete:function (i,j){
        console.log("分类"+i+"数字"+j+"删除");
        this.tables[i].data.splice(j, 1);
        this.$Message.success('已删除');
      },
    },
    created () {
      this.$http.get('/console/nav/get').then(function (data) {
        data = data.body
        for (let i in data) {
          this.$set(this.tables, i, {
            name: data[i].name,
            columns: [
              {
                title: '名称',
                key: 'Name'
              },
              {
                title: '链接',
                key: 'Url'
              },
              {
                title: '操作',
                key: 'Option',
                render: (h, params) => {
                  return h('div', [
                    h('Button', {
                      props: {
                        type: 'primary',
                        size: 'small'
                      },
                      on: {
                        click: () => {
                          this.change(i,params.index)
                        }
                      }
                    }, '修改'),
                    h('Button', {
                      props: {
                        type: 'error',
                        size: 'small'
                      },
                      on: {
                        click: () => {
                          this.delete(i,params.index)
                        }
                      }
                    }, '删除')
                  ])
                }
              }
            ],
            data: []
          })
          for (let j in data[i].menu) {
            this.$set(this.tables[i].data, j, {
              Name: data[i].menu[j].name,
              Url: data[i].menu[j].url
            })
          }
        }
        console.log(this.tables[0])
      }, function (err) {
        this.$Modal.error({
          title: '警告',
          content: '服务器发生错误'
        })
      })
    }
  }
</script>

<style scoped>
    .ivu-collapse-content {
        padding: 0 !important;
    }
</style>
